import { createRouter, createWebHashHistory } from 'vue-router';
import Home from '../views/Home.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/stopwatch',
    name: 'Stopwatch',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "Stopwatch" */ '../views/Stopwatch.vue'),
  },
  {
    path: '/timer',
    name: 'Timer',
    component: () =>
      import(/* webpackChunkName: "Timer" */ '../views/Timer.vue'),
  },
  {
    path: '/toamatotimer',
    name: 'Tomatotimer',
    component: () =>
      import(/* webpackChunkName: "Tomatotimer" */ '../views/TomatoTimer.vue'),
  },
  {
    path: '/studylog',
    name: 'Studylog',
    component: () =>
      import(/* webpackChunkName: "Studylog" */ '../views/Studylog.vue'),
  },
  {
    path: '/ranking',
    name: 'Ranking',
    component: () =>
      import(/* webpackChunkName: "Ranking" */ '../views/Ranking.vue'),
  },
  {
    path: '/setting',
    name: 'Setting',
    component: () =>
      import(/* webpackChunkName: "Setting" */ '../views/Setting.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
